import {H1} from "../../MainStyle";
import styled from "styled-components";
import React from "react";

const StyledMainTextContainer = styled.div`
  width: 429px;
  
  @media(max-width: 700px) {
    width: 255px;
  }
`

const LeftContainerBase = ({className, children}) => {
    const text = children.at(0)
    const newChildren = React.Children.toArray(children).slice(1)

    return (
        <div className={className}>
            <StyledMainTextContainer>
                <H1>{text}</H1>
            </StyledMainTextContainer>
            {newChildren}
        </div>
    )
}

export const LeftContainer = styled(LeftContainerBase)`
  display: flex;
  flex-direction: column;
  width: fit-content;
  margin-top: ${props => props.theme.leftContainerTop};
  
  @media(max-width: 1100px) {
    margin-left: 0;
    margin-top: ${props => props.theme.logoMarginMobile};;
  }
`