import styled from "styled-components";

const StyledRelativeBox = styled.div`
  display: flex;
  padding-left: ${props => props.theme.mainPadding} ;
  padding-right: ${props => props.theme.mainPadding} ;
   margin-bottom: ${props => props.theme.relativeBoxMarginBottom};

  @media (max-width: 1100px) {
    flex-direction: column;
    padding-right: ${props => props.theme.mainMobilePadding} ;
    padding-left: ${props => props.theme.mainMobilePadding} ;
  }

  @media (max-width: 700px) {
    margin-bottom: ${props => props.theme.relativeBoxMarginBottomMobile} ;
  }
`

const LeftBox = styled.div`
  display: flex;
  justify-content: flex-start;

  @media (max-width: 1100px) {
    justify-content: center;
  }
`

const RightBox = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
`

export const RelativeBox = ({children}) => {
    const left = children.at(0)
    const right = children.at(1) // todo zabezpieczy co
    return (
        <StyledRelativeBox>
            <LeftBox>
                {left}
            </LeftBox>
            <RightBox>
                {right}
            </RightBox>
        </StyledRelativeBox>
    )
}