import {convienceProperties} from "../Orange/ConvienceBox/convienceProperties";
import {ConvienceItem} from "../components/ConvienceBox/ConvienceItem";
import React from "react";
import {ConvienceBox} from "../components/ConvienceBox/ConvienceBox";
import {StyledIconItem} from "../components/ConvienceBox/StyledIconItem";

export const PurpleConvienceBox = () => {
    const items = convienceProperties.map((item, index) =>
        <ConvienceItem title={item.title} text={item.text}>
            <StyledIconItem>{React.createElement(item.icon)}</StyledIconItem>
        </ConvienceItem>
    )

    return (
        <ConvienceBox>
            {items}
        </ConvienceBox>
    )
}