import {convienceProperties} from "./convienceProperties";
import {ConvienceItem} from "../../components/ConvienceBox/ConvienceItem";
import {OrangeStyledItem} from "./OrangeStyledItem";
import React from "react";
import {ConvienceBox} from "../../components/ConvienceBox/ConvienceBox";

export const OrangeConvienceBox = () => {
    const items = convienceProperties.map((item, index) =>
        <ConvienceItem title={item.title} text={item.text}>
            <OrangeStyledItem>{React.createElement(item.icon)}</OrangeStyledItem>
        </ConvienceItem>
    )

    return (
        <ConvienceBox>
            {items}
        </ConvienceBox>
    )
}