import styled from "styled-components";
import {ReactComponent as Arrow} from "../../icons/arrow.svg";

const OrnamentArrow = styled(Arrow)`
  fill: white;
  width: ${props => props.theme.biggestArrowWidth} ;
  position: absolute;
  top: ${props => props.theme.biggestArrowTop} ;
  right: calc(${props => props.theme.photographerMarginRight}  - ${props => props.theme.mainPadding} + ${props => props.theme.photographerWidth});
  z-index: -1;
`

export const LeftArrow = styled(OrnamentArrow)`
    display: none;

  @media(max-width: 900px) {
    display: flex;
    right: ${props => props.theme.biggestArrowRight900} ;
    top: ${props => props.theme.biggestArrowTop900} ;
  }

  @media(max-width: 700px) {
    width: ${props => props.theme.biggestArrowWidthMobile} ;
    right: ${props => props.theme.biggestArrowRightMobile} ;
    top: ${props => props.theme.biggestArrowTopMobile} ;
  }
`

export const RightArrow = styled(OrnamentArrow)`
  @media(max-width: 900px) {
    display: none;
  }
`