// import './Header.css'
import styled from "styled-components";
import {Hamburger, Logo} from "../../icons/icons";
import {P} from "../../MainStyle";

const HeaderIconContainer = styled.div`
  display: flex;
  //justify-content: space-between;
  
  svg:first-child {
    width: ${props => props.theme.logoWidth};
    margin-left: ${props => props.theme.logoMargin};
  }
  
  svg:last-child {
    display: none;
  }
  
  @media(max-width: 700px) {
    //background-color: red;
    justify-content: space-between;
    svg:first-child {
      width:  ${props => props.theme.logoMobileWidth};
      margin-left: ${props => props.theme.logoMarginMobile};
    }
    
    svg:last-child {
      margin-right: ${props => props.theme.logoMarginMobile};
      height: 25px;
      display: flex;
    }
  }
`

const MenuContainer = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
  transform: translateX(calc((${props => props.theme.logoMargin} + ${props => props.theme.logoWidth}) * -0.5));
  justify-content: center;
  align-items: center;

  p {
    cursor: pointer;
    margin-left: 15px;
    margin-right: 15px;
    color: ${props => props.theme.paleBaseColor};
  }

  p:hover {
    color: ${props => props.theme.hoverColor};
  }

  @media(max-width: 1000px) {
    transform: translateX(0);
  }
  
  @media(max-width: 700px) {  
    flex-direction: column;  
    align-items: center;  
    overflow: hidden;  
    transition: max-height 500ms;  
    max-height: 0;  
    justify-content: start;
  }
`

const HeaderBase = ({className, options}) => {
    return (
        <div className={className}>
            <HeaderIconContainer>
                <Logo/>
                <Hamburger/>
            </HeaderIconContainer>
            <MenuContainer>
                {options.map(option => <P key={option}>{option}</P>)}
            </MenuContainer>
        </div>
    )
}

export const Header = styled(HeaderBase)`
  display: flex;
  flex-direction: row;
  //align-items: center;
  // margin-top: ${props => props.theme.logoMarginTop};
  background-color: ${props => props.theme.headerBackground};
  height: ${props => props.theme.headerHeight};
  //height: 100px;

  @media(max-width: 700px) {
    flex-direction: column;
    flex-wrap: wrap;

    height: ${props => props.theme.headerMobileHeight};

  }
`