import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Orange from "./Orange/Orange";
import Purple from "./Purple/Purple";
import {BrowserRouter, Link, Route, Switch} from 'react-router-dom';

let pages = [Orange, Purple];

ReactDOM.render((
    <BrowserRouter>
        {
            pages.map(page =>
                <Switch>
                    <Route exact path={`/${page.name.toLowerCase()}`} component={page}/>
                </Switch>
            )
        }
        <Switch>
            <Route exact path={'/'}>
                {
                    pages.map(page =>
                        <div>
                            <Link to={`/${page.name.toLowerCase()}`}>{page.name}</Link>
                        </div>
                    )
                }
            </Route>
        </Switch>
    </BrowserRouter>
    ), document.getElementById('root')
);

