import styled from "styled-components";

export const Photographer = styled.img`
  position: absolute;
  right: calc(${props => props.theme.photographerMarginRight} - ${props => props.theme.mainPadding} );
  z-index: -1;
  top: ${props => props.theme.photographerTop} ;
  max-width: ${props => props.theme.photographerWidth} ;
  width: ${props => props.theme.photographerWidthMobile} ;

  @media (max-width: 1100px) {
    right: calc(${props => props.theme.photographerMarginRight} - ${props => props.theme.mainMobilePadding} );
  }

  @media (max-width: 700px) {
    top: ${props => props.theme.photographerTopMobile} ;
  }
`