import styled from "styled-components";
import {H2, H3} from "../../MainStyle";
import {NavigationPoint} from "../../components/NavigationPoint";

const Id = styled.img`
    width: 100%;
`

const GuaranteeIconContainer = styled.div`
  position: absolute;
  left: calc(${props => props.theme.guaranteeBoxWidth} - 30px);
  top: -40px;
`

const Title = styled(H2)`
  color: ${props => props.theme.guaranteeTextColor};
  margin-bottom: 25px;
`

const Text = styled(H3)`
  color: ${props => props.theme.guaranteeTextColor};
  font-weight: 900;
  line-height: 27px;
  letter-spacing: -0.4px;
  
  margin-bottom: 25px;
`

const Test = styled.div`
  position: relative;
`

const BaseGuaranteeBox = ({className, children}) => {
    const title = children.at(0)
    const text = children.at(1)
    const img = children.at(2) // todo zabezpieczyc
    const icon = children.at(3)
    return (
        <NavigationPoint>
            <GuaranteeIconContainer>
                {icon}
            </GuaranteeIconContainer>
            <div className={className}>
                <Title>{title}</Title>
                <Text>{text}</Text>
                <Id src={img} />
                <GuaranteeIconContainer/>
            </div>
        </NavigationPoint>
    )
}

export const GuaranteeBox = styled(BaseGuaranteeBox)`
  width: ${props => props.theme.guaranteeBoxWidth};
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
  padding-top: 30px;
  background-color: ${props => props.theme.white};

  box-shadow: 0 33.012px 82.5301px -6.60241px rgba(219, 218, 232, 0.8);
  border-radius: 24px;
  
  @media(max-width: 700px) {
    width: ${props => props.theme.guaranteeBoxMobileWidth};
  }

`

